import request from '@/utils/request'
import store from '../store/index'

export default {
  list: function(path) {
    let githubUsername = store.state.configuration.githubUsername
    let reposName = store.state.configuration.reposName
    return request({
      //https://api.github.com/repos/awesomeWDB/webPPT/contents/ppt/demo
      url: `/repos/${githubUsername}/${reposName}/contents/${path}`
    })
  },
  single: function(id) {
    return request({
      url: '/gists/' + id
    })
  },
  create: function(form) {
    let files = {}
    files[form.title] = {
      content: form.content,
      wdbType: 'test'
    }
    return request({
      url: '/gists',
      method: 'post',
      data: {
        "description": form.description,
        "public": true,
        "files": files
      }
    })
  },
  edit: function(form) {
    let files = {}
    files[form.title] = {
      content: form.content,
      wdbType: 'test'
    }
    return request({
      url: '/gists/' + form.id,
      method: 'PATCH',
      data: {
        "description": form.description,
        "public": true,
        "files": files
      }
    })
  },
  delete: function(id) {
    return request({
      url: '/gists/' + id,
      method: 'DELETE',
    })
  },
}



