import axios from 'axios'
const configuration = {
  state: {
    githubUsername: "",
    reposName: "",
    htmlTitle: "",
  },

  mutations: {
    SET_CONFIGURATION: (state, configuration) => {
      state.githubUsername = configuration["githubUsername"] || "shuichi123"
      state.htmlTitle = configuration["htmlTitle"] || `${state.githubUsername}的博客`
      state.reposName = configuration["reposName"] || `webPPT`
    }
  },

  actions: {
    LocalReload({
      commit
    }, configuration) {
      commit('SET_CONFIGURATION', configuration)
    },
    Init({
      commit
    }) {
      return new Promise((resolve, reject) => {
        axios.get('../../../static/configuration.json').then(res => {
          // let configuration = JSON.parse(res.data)
          let configuration = res.data
          commit('SET_CONFIGURATION', configuration)
          resolve(res.data)
        }).catch(err => {
          reject(err)
        })
        // let xmlhttp
        // if (window.XMLHttpRequest) {
        //   xmlhttp = new XMLHttpRequest()
        // } else {
        //   xmlhttp = new ActiveXObject("Microsoft.XMLHTTP")
        // }
        // xmlhttp.open("GET", "../../../static/configuration.json", false)
        // xmlhttp.send()
        // let configuration = JSON.parse(xmlhttp.responseText)
        // commit('SET_CONFIGURATION', configuration)
      })
    }
  }
}

export default configuration



