<template>
  <div>
    <my-nav class="nav"></my-nav>
    <my-main class="main"></my-main>
  </div>
</template>

<script>
  import myNav from './nav'
  import myMain from './main'
  export default {
    components: {
      myNav,
      myMain
    },
    methods: {}
  }
</script>

<style>
  .nav {
    width: 200px;
    height: 100%;
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
  }
  .main{
    margin-left: 200px;
  }
</style>



