<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
  import {mapActions} from 'vuex'
  export default {
    name: 'App',
    methods:{
      ...mapActions(['Init'])
    },
    async created(){
      await this.Init()
      console.log(this.$store.state)
    }
  }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }

  #app {}
</style>



