import Vue from 'vue'
import Router from 'vue-router'
import layout from '@/views/layout/layout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      redirect: 'index',
      component: layout
    },
    {
      path: '/index',
      name: 'index',
      component: layout
    },
    {
      path: '/test',
      name: 'test',
      component: layout
    }
  ]
})



