<template>
  <div>
    <div class="link" v-for="ppt in ppts" :key="ppt.name">
      <el-link :href="`${base_url}/${ppt.path}`" target="_blank">{{ppt.name.split('.html')[0]}}</el-link>
    </div>
  </div>
</template>

<script>
  import contentAPI from '@/api/content'
  import {
    mapGetters
  } from 'vuex'
  export default {
    data() {
      return {
        files: [],
        ppts: []
      }
    },
    computed: {
      ...mapGetters(['githubUsername', 'reposName']),
      base_url() {
        return `https://${this.githubUsername}.github.io/${this.reposName}`
      }
    },
    watch: {
      githubUsername: function(val) {
        contentAPI.list('ppt').then(res => {
          console.log(res)
          this.files = res.data
          this.files.forEach(item => {
            contentAPI.list(item.path).then(sub_res => {
              console.log(sub_res)
              sub_res.data.forEach(item => {
                if(item.name.includes('.html') && item.name!="template.html"){
                  this.ppts.push(item)
                }
              })
              console.log(this.ppts)
            })
          })
        })
      }
    }
  }
</script>

<style scoped>
  .link {
    display: block;
    text-align: center;
    margin: 20px 0;
  }
</style>



